/*
 CSS Reset
*/
*, *::before, *::after {
    box-sizing: border-box;
  }
  
  * {
    margin: 0;
    padding: 0;
  }

  html, body {
    height: 100%;
  }
  
  body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
  }

  img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
  }
  
  input, button, textarea, select {
    font: inherit;
  }

  p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
  }


:root {
    --bg-color: #1e1e1e;
    --text: #ccc;
    --font-stack: 'Inter', sans-serif;
}

body {
    background-color: var(--bg-color);
    color: var(--text);
    font-family: var(--font-stack);
    display: grid;
    place-content: center;
    height: 100%;
}

main {
  min-width:400px;
  max-width: 800px;
  height: 100vh;
  padding:0.5rem;
}


.header {
  display: flex;
  flex-wrap: nowrap;
  align-items: flex-start;
  justify-content: space-between;
  padding: 0.5rem;
}

.content {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0.5rem 0;
}

.note {
  padding: 0.5rem;
  border-radius: 0.25rem;
  margin-bottom: 1rem;
}

.note:hover {
  background-color: #1b1b1b;
}

.noteTitle {
  font-weight: 700;
}

.noteInfo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin: 0.5rem 0;
}

.noteInfoItem {
  opacity: 0.5;
  list-style: none;
}

.noteOptions {
  display: flex;  
}

.icon{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.35rem;
  margin: 0 0.5rem;
  border-radius: 0.25rem;
}

.icon:hover {
  cursor: pointer;
  background-color: #111;
  outline: 1px solid var(--text);
}

#icon {
  margin-right:0.5rem ;
}

path {
  stroke: var(--text);
}

.bg-hover {
  background-color: red;
}

.formContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  margin-top: 1rem;
}

.noteTitleInput {
  border: 1px solid red;
  border: 0;
  outline: 0;
  width: 100%;
  background-color: #1b1b1b;
  padding: 0.5rem;
  color: #ccc;
}

.textarea {
  width: 100%;
  background-color: #1b1b1b;
  outline: 0;
  border: 0;
  resize:none;
  padding: 0.5rem;
  color: #fff;
}

.btnSubmit {
  margin: 0.5rem 0;
  width: 120px;
  height: 40px;
  border: 0;
  outline: 0;
  border-radius: 0.2rem;
  background-color: #0d0d0d;
  color: #fff;
}

/* fix this */
@media (min-width:0px) and (max-width:1000px) {
  main {
    min-width: 300px;
    max-width: 380px;
  }
}